import React, { Component } from 'react';

import {
    Button,
    Container,
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem } from 'reactstrap';
import { withRouter, NavLink } from 'react-router-dom';
import { connect } from 'react-redux';

import './header.css';

class Header extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.handleScroll = this.handleScroll.bind(this);
        this.state = {
            isOpen: false,
            scrolled: window.scrollY > 10
        };

    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
        this.handleScroll();
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    componentWillReceiveProps() {
        this.closeNav();
        window.scrollTo(0,0);
        setTimeout(() => {
            this.handleScroll();
        }, 10);
    }

    handleScroll() {
        this.setState({
            scrolled: window.scrollY > 10
        });
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    closeNav() {
        this.setState({
           isOpen: false
        });
    }

    render() {
        const isColorDark = this.props.header.style === 'DARK' ? 'navbar-dark' : 'navbar-light';
        const isBackgroundTransparent = this.props.header.opacity !== 'ALWAYS_FULL' && !this.state.scrolled && !this.state.isOpen;
        const backgroundColor = isBackgroundTransparent ? '' : this.props.header.style === 'DARK' ? 'dark' : 'light';
        return (
            <header>
                <Navbar fixed="top" expand="md" className={isColorDark} color={ backgroundColor }>
                    <Container fluid>
                        <NavbarBrand href="/">
                            <span className="icon-logo-juz-entertainment"/>
                        </NavbarBrand>
                        <Button className="round-button round-button-red ml-auto mr-4 my-3">РУС</Button>
                        <NavbarToggler onClick={this.toggle}>
                            <span className="navbar-toggler-icon">
                                <span className="navbar-toggler-icon-line"/>
                            </span>
                        </NavbarToggler>
                        <Collapse isOpen={this.state.isOpen} navbar>
                            <Nav className="mx-auto main-nav" navbar>
                                <NavItem>
                                    <NavLink exact to={'/'} className="nav-link">Главная</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink to={'/artists'} className="nav-link">Артисты</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink to={'/songs'} className="nav-link">Медиатека</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink to={'/concerts'} className="nav-link">Концерты</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink to={'/contacts'} className="nav-link">Контакты</NavLink>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </Container>
                </Navbar>
            </header>
        );
    }
}


const mapStateToProps = (state) => ({
    header: state.header
});

export default withRouter(connect(mapStateToProps)(Header));


