import React, { Component } from 'react';


export default class AudioGlobal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            songs: [],
            playing: null
        };
        this.audioPlayer = React.createRef();
        this.playSong = this.playSong.bind(this);
    }

    playSong(song) {
        this.setState({
           playing: song.url
        });
        console.log(this.audioPlayer.current.play());
        setInterval(()=>{
            console.log(this.audioPlayer, this.state.audioPlayer.current.currentTime, this.state.audioPlayer.current.duration);
        },1000)
    }

    render() {
        return (
            <audio src={this.state.playing}>
                Audio is not supported by your browser
            </audio>
        )
    }

}


